






















import service from '../../service.js';

export default {
	data() {
		return {
			account: '',
			password: '',
			email: ''
		}
	},
	methods: {
		register() {
			if (this.email.length < 3 || !~this.email.indexOf('@')) {
				uni.showToast({
					icon: 'none',
					title: 'Your email address is not valid'
				});
				return;
			}
			if (this.account.length < 5) {
				uni.showToast({
					icon: 'none',
					title: 'Your name musty be at least 5 characters'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: 'Your password must be at least 6 characters'
				});
				return;
			}
			const data = {
				account: this.account,
				password: this.password,
				email: this.email
			}
			
			uni.request({
				url: 'http://localhost:8080/servlat/register',
				method: 'POST',
				data: {
					username: this.username,
					password: this.password,
				},

				success: (res) => {
					uni.showToast({
						icon: 'none',
						title: 'Register Complete'
					});
					uni.navigateTo({
						url: '../login/login',
					});
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: 'The network is out of order,please try again later'
					});
				}
			});
		}

	}
}
